<template>
  <div>
    <div class="top" style="float: left">
    <span style="font-size: 20px;font-weight: bold;">全勤奖：</span>
      <el-switch
        v-if="this.attendanceList.length==0"
        v-model="value0"
        active-color="#13ce66"
        inactive-color="#ff4949"
        active-text="有"
        inactive-text="无"
        @change="getChange(value0)"
        style="top:-3px;left:-15px">
      </el-switch>
      <el-switch
        v-if="this.attendanceList.length!=0"
        v-model="value1"
        active-color="#13ce66"
        inactive-color="#ff4949"
        active-text="有"
        inactive-text="无"
        @change="getChange(value0)"
        style="top:-3px;left:-15px">
      </el-switch>
    </div>
    <div class="main" style="position: relative;top: 50px">
    <div v-if="this.attendanceList.length==0">
        <span style="font-size: 30px;font-weight: bold;position: relative;left: -150px;top: 100px">未设置全勤奖，无考勤规则</span>
    </div>
    <div v-if="this.attendanceList.length!=0">
            <el-table
                :data="attendanceList"
                style="width: 100%;border-color:#3a8ee6;top:-20px"
                :default-sort = "{prop: 'num', order: 'ascending'}"
                border
                :header-cell-style="{background:'#eef1f6',color:'#333333'}"
                @current-change="handleCurrentsChange"
              >
                <el-table-column
                  prop="name"
                  label="奖金名称"
                  sortable
                  align="center"
                  width="150"
                >
                </el-table-column>
                <el-table-column
                  prop="time"
                  label="迟到早退时间限制（小时）"
                  sortable
                  align="center">
                </el-table-column>
                <el-table-column
                  prop="times"
                  label="迟到早退次数限制"
                  sortable
                  align="center">
                </el-table-column>
                <el-table-column
                  prop="rate"
                  label="奖金比例（占工资的比例）"
                  sortable
                  align="center">
                </el-table-column>
                <el-table-column
                prop="fixedAward"
                label="固定奖金"
                sortable
                align="center">
                </el-table-column>
                 <el-table-column
                 prop="operate"
                 label="操作"
                 align="center">
                     <el-button type="warning" @click="showChange()" style="padding: 12px 15px">修改</el-button>
                </el-table-column>
            </el-table>
    </div>
      <!-- 全勤奖设置更改弹框 -->
      <div class="change0" v-if="flag3">
        <div class="title">
          <span>更改提示</span>
          <i class="el-icon-close" @click="closeChange()"></i>
        </div>
        <div class="main">
          <i class="el-icon-warning"></i>
          <span>您确定要更改全勤奖设置吗？</span>
        </div>
        <div class="deleteButton">
          <el-button  type="danger" @click="changeInfo()" style="padding: 12px 15px">确定</el-button>
          <el-button  @click="closeChange()" style="padding: 12px 15px">取消</el-button>
        </div>
      </div>

      <!-- 修改弹框 -->
      <div v-if="flag5" class="changeAttendance" >
        <div class="title">
          <h2>修改考勤规则</h2>
          <i class="el-icon-close" @click="closeChange2()"></i>
        </div>
        <div class="change1">
          <el-form ref="form"  label-width="80px">
            <el-form-item label="奖金名称" style="position: relative;left: 90px;top:-25px">
              <el-input v-model="changeList.name" style="position: relative;top:0px;left:-55px;width:150px" readonly></el-input>
            </el-form-item>
            <el-form-item label="时间限制" style="position: relative;left: 90px;top:-25px;">
              <el-input v-model="changeList.time" style="position: relative;top:0px;left:-55px;width:150px"></el-input>
            </el-form-item>
            <el-form-item label="次数限制" style="position: relative;left: 90px;top:-25px;">
              <el-input v-model="changeList.times" style="position: relative;top:0px;left:-55px;width:150px"></el-input>
            </el-form-item>
            <el-form-item label="奖金比例" style="position: relative;left: 90px;top:-25px;margin-top: 15px">
              <el-input v-model="changeList.rate" style="position: relative;top:0px;left:-55px;width:150px"></el-input>
            </el-form-item>
            <el-form-item label="固定奖金" style="position: relative;left: 90px;top:-25px">
              <el-input v-model="changeList.fixedAward" style="position: relative;top:0px;left:-55px;width:150px"></el-input>
            </el-form-item>
            <div class="saveButton">
              <el-form-item>
                <el-button type="primary" @click="changeAttendance()" style="padding: 12px 15px">修改</el-button>
              </el-form-item>
            </div>
          </el-form>
        </div>
      </div>

    </div>
  </div>
</template>


<script>
    export default {
        inject:['reload'],
        data() {
            return {
                attendanceList:[],
                addList: [],
                changeList: [],
                value: false,
                value0: false,
                value1: true,
                // flag1: '',
                // flag2: '',
                flag3: false,
                // flag4: false,
                flag5: false,
                // flag6: false,
            }
        },
        created(){
            let _this = this;
            this.$axios
                .get("/getAllAttendance")
                .then(function(res) {
                    if (res.data) {
                        _this.attendanceList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
        },


        methods:{
            getChange(value) {
                this.flag3 = !this.flag3;
            },
            changeInfo(){
                this.flag3 = !this.flag3;
                this.value = true;
                if(this.attendanceList.length!=0){
                    this.value = false;
                }
                this.$axios
                    .post("/updateAttendanceStatus",
                        {
                            values:this.value
                        }
                    )
                    .then(successResponse  => {
                        if (successResponse.data.code === 200) {
                            this.reload();
                            this.$message({
                                message: '全勤奖设置更改成功！',
                                type: 'success',
                                center: true
                            });
                        }else {
                            this.reload();
                            this.$message.error('全勤奖设置更改失败！');
                        }
                    })
                    .catch(function(err) {
                        if (err.response) {
                            console.log(err.response);
                        }
                    });
            },

            changeAttendance() {
                let _this = this;
                this.$axios
                    .post('/updateAttendance', {
                        id: this.changeList.id,
                        time: this.changeList.time,
                        times: this.changeList.times,
                        rate: this.changeList.rate,
                        fixedAward: this.changeList.fixedAward
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag5 = !this.flag5;
                            this.reload();
                            this.$message({
                                message: '修改成功！！！',
                                type: 'success',
                                center: true
                            });
                        } else {
                            this.reload();
                            this.$message.error('修改失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('修改后信息填写不正确！');
                    });
            },
            closeChange() {
                if(this.attendanceList.length!=0){
                    this.value1 = true;
                }else {
                    this.value0 = false;
                }
                this.flag3 = !this.flag3;
                this.$message("操作已取消");
            },
            showChange() {
                this.flag5 = !this.flag5;
            },
            closeChange2() {
                this.flag5 = !this.flag5;
                this.$message("操作已取消");
                this.reload();
            },
            handleCurrentsChange(val) {
                this.addList = val;
                this.changeList = val;
                this.deleteList = val;
            },
         }
    };
</script>
<style>

  .el-switch__core{
    height: 20px !important;
    line-height: 20px !important;
    width: 46px !important;
  }
  .el-switch__label--left{
    position: relative;
    left: 46px;
    color: #fff;
    z-index: -1111;
  }
  .el-switch__label--right{
    position: relative;
    right: 46px;
    color: #fff;
    z-index: -1111;
  }
  .el-switch__label--right.is-active{
    z-index: 1111;
    color: #fff !important;
  }
  .el-switch__label--left.is-active{
    z-index: 1111;
    color: #fff !important;
  }
  .el-switch__core:after{
    top: 2px;
  }
  .button {
    padding-bottom: 20px;
    float: left;
    position: relative;
    left: -178px;

  }

  .el-table--border:after,
  .el-table--group:after,
  .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }
  .top {
    height: 10px;
  }
  .change0 {
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 180px;
    border: 1px solid #dcdfe6;
    left: -150px;
    top: 200px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
  }
  .title {
    width: 100%;
    height: 45px;
    line-height: 45px;
  }
  .title span {
    float: left;
    margin-left: 20px;
  }
  .change0 .title i {
    float: right;
    margin-top: 10px;
    margin-right: 10px;
  }
  .change0 .main {
    height: 60px;
    width: 100%;
    line-height: 50px;
    margin-top: 15px;
    margin-left: 0px;
  }
  .change0 .main i {
    color: #e6a23c;
    font-size: 20px;
    margin-right: 10px;
  }
  .change0 .main span {
    margin-bottom: 5px;
  }
  .change0 .deleteButton {
    float: right;
    margin-right: 20px;
    margin-top: 5px;
  }
  .addAttendance {
    position: absolute;
    z-index: 1001;
    width: 500px;
    height: 450px;
    left: 0;
    top: 300px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .addAttendance .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
  }
  .addAttendance .title i {
    position: relative;
    top: -55px;
    right: -230px;
    color: black;
    font-size: 20px;
  }
  .addAttendance .add {
    margin-right: 30px;
    margin-top: 30px;
  }
  .addAttendance .addButton {
    margin-left:-25px;
    margin-top: -80px;
  }
  .addAttendance .addButton .el-button {
    width: 80px;
  }
  .addAttendance .el-select {
    width: 280px;
  }
  .changeAttendance {
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 420px;
    left: 0;
    top: 300px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .changeAttendance .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
  }
  .changeAttendance .title i {
    position: relative;
    top: -55px;
    right: -180px;
    color: black;
    font-size: 20px;
  }
  .changeAttendance .change1 {
    margin-right: 60px;
    margin-top: 20px;
  }

  .changeAttendance .saveButton {
    margin-left:-20px;
    margin-top: -30px;
  }
  .changeAttendance .saveButton .el-button {
    width: 80px;
  }
  .changeAttendance .el-select {
    width: 280px;
  }
</style>



