<template>
  <div class="cut">
    <div class="topButton">
      <el-button type="primary" @click="showAdd()" style="padding: 12px 15px;float: left">添加</el-button>
    </div>
    <!-- 表格 -->
    <div class="list">
      <el-table
        :data="cutList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%;border-color:#3a8ee6"
        :default-sort = "{prop: 'num', order: 'ascending'}"
        border
        :header-cell-style="{background:'#eef1f6',color:'#333333'}"
        @current-change="handleCurrentsChange"
      >
        <el-table-column
          prop="num"
          label="序号"
          align="center"
          sortable
          width="150"
        >
        </el-table-column>
        <el-table-column
          prop="ename"
          label="员工姓名"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="name"
          label="扣款种类"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="cut"
          label="扣款金额"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="time"
          label="发放时间"
          sortable
          align="center">
        </el-table-column>
        <el-table-column
          prop="operate"
          label="操作"
          align="center">
          <template scope="scope" >
            <el-button type="warning" @click="showChange()" style="padding: 12px 15px">修改</el-button>
            <el-button type="danger"  @click="showDelete()" style="padding: 12px 15px">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="cutList.length"
        ></el-pagination>
      </div>
    </div>

    <!-- 添加弹框 -->
    <div v-if="flag" class="addCut">
      <div class="title">
        <h2>新增月扣款信息</h2>
        <i class="el-icon-close" @click="closeAdd()"></i>
      </div>

      <div class="add">

        <el-form ref="form"  label-width="80px">
          <el-form-item label="员工姓名" style="position:relative;top:-30px;left:80px">
            <el-select v-model="addList.ename" placeholder="请选择员工姓名" style="width:150px;position:relative;left:-60px">
              <el-option  v-for="item in employerList"
                          :label="item.name"
                          :value="item.name"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="扣款种类" style="position:relative;top:-30px;left:80px">
            <el-select v-model="addList.name" placeholder="请选择扣款种类" style="width:150px;position:relative;left:-60px">
              <el-option  v-for="item in cutcategoryList"
                          :label="item.name"
                          :value="item.name">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="扣款金额" style="position:relative;top:-30px;left:80px">
            <el-input v-model="addList.cut" style="width:150px;position:relative;left:-60px"></el-input>
          </el-form-item>
          <el-form-item label="发放时间" style="position:relative;top:-30px;left:80px">
            <el-select v-model="addList.time" placeholder="请选择发放时间" style="width:150px;position:relative;left:-60px">
              <el-option  v-for="item in createdateList"
                          :label="item.name"
                          :value="item.name">
              </el-option>
            </el-select>
          </el-form-item>
          <div class="addButton">
            <el-form-item>
              <el-button type="primary"  @click="addCut()" style="padding: 12px 15px">添加</el-button>
              <el-button @click="addRest()" style="padding: 12px 15px">重置</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>

    <!-- 修改弹框 -->
    <div v-if="flag2" class="changeCut">
      <div class="title">
        <h2>修改月扣款信息</h2>
        <i class="el-icon-close" @click="closeChange()"></i>
      </div>

      <div class="change">
        <el-form ref="form"  label-width="80px">
          <el-form-item label="员工姓名" style="position:relative;top:-30px;left:80px" class="readonly">
            <el-input v-model="changeList.ename" style="width:150px;position:relative;left:-60px" readonly></el-input>
          </el-form-item>
          <el-form-item label="补贴种类" style="position:relative;top:-30px;left:80px" class="readonly">
            <el-input v-model="changeList.name" style="width:150px;position:relative;left:-60px;" readonly></el-input>
          </el-form-item>
          <el-form-item label="补贴金额" style="position:relative;top:-30px;left:80px">
            <el-input v-model="changeList.cut" style="width:150px;position:relative;left:-60px"></el-input>
          </el-form-item>
          <el-form-item label="发放时间" style="position:relative;top:-30px;left:80px">
            <el-select v-model="changeList.time" placeholder="请选择发放时间" style="width:150px;position:relative;left:-60px">
              <el-option  v-for="item in createdateList"
                          :label="item.name"
                          :value="item.name">
              </el-option>
            </el-select>
          </el-form-item>

          <div class="saveButton">
            <el-form-item>
              <el-button type="primary" @click="changeCut()" style="padding: 12px 15px">修改</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>

    <!-- 删除弹框 -->
    <div class="delete" v-if="flag3">
      <div class="title">
        <span>删除提示</span>
        <i class="el-icon-close" @click="closeDelete()"></i>
      </div>
      <div class="main">
        <i class="el-icon-warning"></i>
        <span>您确定要删除此月补贴信息吗？</span>
      </div>
      <div class="deleteButton">
        <el-button  type="danger" @click="deleteInfo()" style="padding: 12px 15px">确定</el-button>
        <el-button  @click="closeDelete()" style="padding: 12px 15px">取消</el-button>
      </div>
    </div>

    <div class="popContainer" v-if="flag"></div>
    <div class="popContainer" v-if="flag2"></div>
    <div class="popContainer" v-if="flag3"></div>

  </div>
</template>

<script>
    export default {
        data() {
            return {
                cutList: [],
                flag: false,
                flag2: false,
                flag3: false,
                currentPage: 1, //初始页
                pagesize: 10,
                flag: false,
                radio: '0',
                addList: [],
                changeList: [],
                deleteList: [],
                employerList:[],
                createdateList:[],
                cutcategoryList:[],
            };
        },

        created(){
            let _this = this;
            this.$axios
                .get("/getAllCut")
                .then(function(res) {
                    if (res.data) {
                        _this.cutList= res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getAllCutcategory")
                .then(function(res) {
                    if (res.data) {
                        _this.cutcategoryList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getAllEmployer")
                .then(function(res) {
                    if (res.data) {
                        _this.employerList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getAllCreatedate")
                .then(function(res) {
                    if (res.data) {
                        _this.createdateList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
        },
        inject: ["reload"],
        methods: {
            addCut() {
                let _this = this;
                this.$axios
                    .post('/addCut', {
                        ename: this.addList.ename,
                        category: this.addList.name,
                        cut: this.addList.cut,
                        time: this.addList.time
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag = !this.flag;
                            this.reload();
                            this.$message({
                                message: '添加成功！！！',
                                type: 'success',
                                center: true
                            });
                        }else if(successResponse.data.code === 100){
                            this.reload();
                            this.$message.error('该员工已存在该月份该扣款种类记录！');
                        } else {
                            this.reload();
                            this.$message.error('添加失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('添加信息填写不正确！');
                    });
            },

            changeCut() {
                let _this = this;
                this.$axios
                    .post('/updateCut', {
                        id: this.changeList.id,
                        ename: this.changeList.ename,
                        category: this.changeList.name,
                        cut: this.changeList.cut,
                        time: this.changeList.time
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag2 = !this.flag2;
                            this.reload();
                            this.$message({
                                message: '修改成功！！！',
                                type: 'success',
                                center: true
                            });
                        } else {
                            this.reload();
                            this.$message.error('修改失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('修改后信息填写不正确！');
                    });
            },
            deleteInfo() {
                let _this = this
                this.$axios
                    .post('/deleteCut', {
                        id:this.deleteList.id
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag3 = !this.flag3;
                            this.reload();
                            this.$message({
                                message: '删除成功！！！',
                                type: 'success',
                                center: true
                            });
                        }else {
                            this.reload();
                            this.$message.error('删除失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('删除失败！');
                    });
            },


            showAdd() {
                this.flag = !this.flag;
                this.addList = [
                    {
                        shown :"0"
                    },
                ];
            },

            showChange() {
                this.flag2 = !this.flag2;
            },
            showDelete() {
                this.flag3 = !this.flag3;
            },

            closeAdd() {
                this.flag = !this.flag;
                this.$message("操作已取消");
                this.reload();
            },

            addRest() {
                this.addShown="否",
                    this.addList = [
                    ]
            },

            closeChange() {
                this.flag2 = !this.flag2;
                this.$message("操作已取消");
                this.reload();
            },
            closeDelete() {
                this.flag3 = !this.flag3;
                this.$message("操作已取消");
                this.reload();
            },

            handleCurrentsChange(val) {
                this.currentRow = val;
                this.addList = val;
                this.changeList = val;
                this.deleteList = val;
            },
            handleSizeChange: function(size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage) {
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
            }
        },

    }
</script>
<style>
  .readonly .el-input__inner {
    background-color: white;
  }
  .topButton {
    width: 100%;
    height: 45px;
    line-height: 50px;
    padding-bottom: 10px;
  }
  .el-submenu .el-menu-item:hover {
    background-color: rgb(232, 243, 255);
  }
  .list {
    position: relative;
  }
  .page {
    position: absolute;
    right: 0;
    bottom: -50px;
  }

  .el-table--border:after,
  .el-table--group:after,
  .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }

  .addCut {
    position: absolute;
    z-index: 1001;
    width: 420px;
    height: 380px;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .addCut .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
  }
  .addCut .title i {
    position: relative;
    top: -55px;
    right: -170px;
    color: black;
    font-size: 20px;
  }
  .addCut .add {
    margin-right: 30px;
    margin-top: 30px;
  }
  .addCut .addButton {
    margin-right:40px;
    margin-top: -30px;
  }
  .addCut .addButton .el-button {
    width: 80px;
  }
  .addCut .el-select {
    width: 280px;
  }
  .changeCut{
    position: absolute;
    z-index: 1001;
    width: 420px;
    height: 380px;
    left: 0;
    top: 0px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .changeCut .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
  }
  .changeCut .title i {
    position: relative;
    top: -55px;
    right: -170px;
    color: black;
    font-size: 20px;
  }
  .changeCut .change {
    margin-right: 30px;
    margin-top: 30px;
  }

  .changeCut .saveButton {
    margin-right:50px;
    margin-top: -30px;
    position: relative;
  }

  .changeCut .saveButton .el-button {
    width: 80px;
  }
  .changeCut .el-select {
    width: 280px;
  }
  .delete {
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 180px;
    border: 1px solid #dcdfe6;
    left: 0;
    top: -50px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
  }
  .title {
    width: 100%;
    height: 45px;
    line-height: 45px;
  }
  .title span {
    float: left;
    margin-left: 20px;
  }
  .delete .title i {
    float: right;
    margin-top: 10px;
    margin-right: 10px;
  }
  .delete .main {
    height: 60px;
    width: 100%;
    line-height: 50px;
    margin-top: 15px;
    margin-left: 0px;
  }
  .delete .main i {
    color: #e6a23c;
    font-size: 20px;
    margin-right: 10px;
  }
  .delete .main span {
    margin-bottom: 5px;
  }
  .delete .deleteButton {
    float: right;
    margin-right: 20px;
    margin-top: 5px;
  }
  .popContainer {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 500;
  }
</style>



