<template>
  <div class="userdefault1">
    <div class="topButton">
      <el-button type="primary" @click="showAdd()" style="padding: 12px 15px;float: left">添加</el-button>
      <el-button type="primary" @click="back()" style="padding: 12px 15px;float: left">返回</el-button>
    </div>
    <!-- 表格 -->
    <div class="list">
      <el-table
        :data="userdefault1List.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%;border-color:#3a8ee6"
        :default-sort = "{prop: 'num', order: 'ascending'}"
        border
        :header-cell-style="{background:'#eef1f6',color:'#333333'}"
        @current-change="handleCurrentsChange"
      >
        <el-table-column
          prop="eid"
          label="员工工号"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="ename"
          label="姓名"
          align="center">
        </el-table-column>
        <el-table-column
          prop="regularpay"
          label="基本工资"
          align="center">
        </el-table-column>
        <el-table-column
          prop="insurancebase"
          label="社保基数"
          align="center">
        </el-table-column>
        <el-table-column
          prop="updatedate"
          label="更新时间"
          align="center">
        </el-table-column>
        <el-table-column
          prop="note"
          label="备注"
          align="center">
        </el-table-column>
        <el-table-column
          prop="operate"
          label="操作"
          align="center">
          <template scope="scope" >
            <el-button type="warning"  @click="showChange()" style="padding: 12px 15px">修改</el-button>
            <el-button type="danger"  @click="showDelete()" style="padding: 12px 15px">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="userdefault1List.length"
        ></el-pagination>
      </div>
    </div>

    <!-- 添加弹框 -->
    <div v-if="flag" class="addUserdefault1">
      <div class="title">
        <h2>新增员工工资信息</h2>
        <i class="el-icon-close" @click="closeAdd()"></i>
      </div>

      <div class="add">
        <el-form ref="form"  label-width="80px">
          <el-form-item label="员工姓名" style="position: relative;left: 110px;top:-25px">
              <span v-model="addList.ename" style="position: relative;left:-75px;width:200px">{{this.employerList[0].name}}</span>
          </el-form-item>
          <el-form-item label="社保基数" style="position: relative;left: 110px;top:-25px ">
            <el-input v-model="addList.insurancebase" style="position: relative;left:-75px;width:200px"></el-input>
          </el-form-item>
          <el-form-item label="基本工资" style="position: relative;left: 110px;top:-25px">
            <el-input v-model="addList.regularpay" style="position: relative;left:-75px;width:200px"></el-input>
          </el-form-item>
          <el-form-item label="更新时间" style="position: relative;left: 110px;top:-25px">
            <el-date-picker
              v-model="addList.updatedate"
              type="date"
              placeholder="选择日期"
              style="width:200px;position:relative;left:-75px"
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item label="备注" style="position: relative;left: 110px;top:-25px">
            <el-input v-model="addList.note" style="position: relative;left:-75px;width:200px"></el-input>
          </el-form-item>

          <div class="addButton">
            <el-form-item>
              <el-button type="primary" @click="addUserdefault1()" style="padding: 12px 15px">添加</el-button>
              <el-button @click="addRest()" style="padding: 12px 15px">重置</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>

    <!-- 修改弹框 -->
    <div v-if="flag2" class="changeUserdefault1">
      <div class="title">
        <h2>修改员工工资信息</h2>
        <i class="el-icon-close" @click="closeChange()"></i>
      </div>

      <div class="change">
        <el-form ref="form" label-width="80px">
          <el-form-item label="员工姓名" style="position: relative;left: 110px;top:-25px">
            <el-input v-model="changeList.ename" style="position: relative;left:-75px;width:150px" readonly></el-input>
          </el-form-item>
          <el-form-item label="社保基数" style="position: relative;left: 110px;top:-25px ">
            <el-input v-model="changeList.insurancebase" style="position: relative;left:-75px;width:150px"></el-input>
          </el-form-item>
          <el-form-item label="基本工资" style="position: relative;left: 110px;top:-25px">
            <el-input v-model="changeList.regularpay" style="position: relative;left:-75px;width:150px"></el-input>
          </el-form-item>
          <el-form-item label="更新时间" style="position: relative;left: 110px;top:-25px">
            <el-date-picker
              v-model="changeList.updatedate"
              type="date"
              placeholder="选择日期"
              style="width:150px;position:relative;left:-75px"
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item label="备注" style="position: relative;left: 110px;top:-25px">
            <el-input v-model="changeList.note" style="position: relative;left:-75px;width:150px"></el-input>
          </el-form-item>

          <div class="saveButton">
            <el-form-item>
              <el-button type="primary" @click="changeUserdefault1()" style="padding: 12px 15px">修改</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>

    <!-- 删除弹框 -->
    <div class="delete" v-if="flag3">
      <div class="title">
        <span>删除提示</span>
        <i class="el-icon-close" @click="closeDelete()"></i>
      </div>
      <div class="main">
        <i class="el-icon-warning"></i>
        <span>您确定要删除此员工工资信息吗？</span>
      </div>
      <div class="deleteButton">
        <el-button  type="danger" @click="deleteInfo()" style="padding: 12px 15px">确定</el-button>
        <el-button  @click="closeDelete()" style="padding: 12px 15px">取消</el-button>
      </div>
    </div>

    <div class="popContainer" v-if="flag"></div>
    <div class="popContainer" v-if="flag2"></div>
    <div class="popContainer" v-if="flag3"></div>

  </div>
</template>

<script>
    export default {
        data() {
            return {
                userdefault1List: [],
                id:this.$route.query.id,
                flag: false,
                flag2: false,
                flag3: false,
                currentPage: 1, //初始页
                pagesize: 10,
                flag: false,
                // addUserdefault1Style:{
                //     height:'630px'
                // },

                employerList:[

                ],
                cutcategoryList:[

                ],
                allowancecategoryList:[

                ],
                addList: [
                    {
                        eid:"",
                        updatedate:"",
                        ename:"",
                        cutids:"",
                        insurancebase:"",
                        note:"",
                    }
                ],
                changeList: [
                    {
                        eid:"",
                        updatedate:"",
                        ename:"",
                        cutids:"",
                        insurancebase:"",
                        note:"",
                    }
                ],
                FormArr: [
                    {
                        index: 0,
                        value: ''
                    }
                ],
                FormArr1: [
                    {
                        index: 0,
                        value: ''
                    }
                ],
                deleteList: [
                    {
                        id:""
                    }
                ],

            };
        },
        mounted(){

        },
        created(){
            let _this = this;
            this.$axios
                .get("/getUserdefault1ByEid?eid="+this.$route.query.id)
                .then(function(res) {
                    if (res.data) {
                        _this.userdefault1List = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
            this.$axios
                .get("/getEmployerById?id="+this.$route.query.id)
                .then(function(res) {
                    if (res.data) {
                        _this.employerList = res.data;
                    }
                })
                .catch(function(err) {
                    if (err.response) {
                        console.log(err.response);
                    }
                });
        },
        inject: ["reload"],
        methods: {
            addUserdefault1() {
                let _this = this
                    let date =new Date(this.addList.updatedate)
                    this.updatedate =date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate();
                    this.$axios
                        .post('/addUserdefault1', {
                            ename: this.employerList[0].name,
                            insurancebase: this.addList.insurancebase,
                            regularpay: this.addList.regularpay,
                            updatedate: this.updatedate,
                            note: this.addList.note,
                        })
                        .then(successResponse => {
                            if (successResponse.data.code === 200) {
                                this.flag = !this.flag;
                                this.reload();
                                this.$message({
                                    message: '添加成功！！！',
                                    type: 'success',
                                    center: true
                                });
                            } else {
                                this.reload();
                                this.$message.error('添加失败！');
                            }
                        })
                        .catch(failResponse => {
                            this.reload();
                            this.$message.error('添加信息填写不正确！');
                        });
            },

            changeUserdefault1() {
                let _this = this
                let date =new Date(this.changeList.updatedate)
                this.updatedate =date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate();
                this.$axios
                    .post('/updateUserdefault1', {
                        ename: this.changeList.ename,
                        insurancebase: this.changeList.insurancebase,
                        regularpay: this.changeList.regularpay,
                        updatedate: this.updatedate,
                        note: this.changeList.note,
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag = !this.flag;
                            this.reload();
                            this.$message({
                                message: '修改成功！！！',
                                type: 'success',
                                center: true
                            });
                        } else {
                            this.reload();
                            this.$message.error('修改失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('修改信息填写不正确！');
                    });
            },

            deleteInfo() {
                let _this = this
                this.$axios
                    .post('/deleteUserdefault1', {
                        id:this.deleteList.id
                    })
                    .then(successResponse => {
                        if (successResponse.data.code === 200) {
                            this.flag3 = !this.flag3;
                            this.reload();
                            this.$message({
                                message: '删除成功！！！',
                                type: 'success',
                                center: true
                            });
                        }else {
                            this.reload();
                            this.$message.error('删除失败！');
                        }
                    })
                    .catch(failResponse => {
                        this.reload();
                        this.$message.error('删除失败！');
                    });
            },

            back() {
                //指定跳转地址
                this.$router.push({
                    path: "/empInfoManagement",
                })
            },

            showAdd() {
                if(this.userdefault1List.length==0){
                    this.flag = !this.flag;
                    this.addList = [
                        {
                            ename:this.userdefault1List.ename,
                        }
                    ];
                }else{
                    this.$message.error('该员工工资信息已存在，无法新增！');
                }
            },

            showChange() {
                this.flag2 = !this.flag2;
            },

            showDelete() {
                this.flag3 = !this.flag3;
            },

            closeAdd() {
                this.flag = !this.flag;
                this.$message("操作已取消");
                this.reload();
            },

            addRest() {
                this.addList = [];
                this.FormArr = [
                    {
                        index: 0,
                        value: ''
                    }
                ];
            },

            closeChange() {
                this.flag2 = !this.flag2;
                this.$message("操作已取消");
                this.reload();
            },

            closeDelete() {
                this.flag3 = !this.flag3;
                this.$message("操作已取消");
                this.reload();
            },

            handleCurrentsChange(val) {
                this.currentRow = val;
                this.addList = val;
                this.changeList = val;
                this.deleteList = val;
            },
            handleSizeChange: function(size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage) {
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
            }
        },

    }
</script>
<style>
  .topButton {
    width: 100%;
    height: 45px;
    line-height: 50px;
    padding-bottom: 10px;
  }
  .el-submenu .el-menu-item:hover {
    background-color: rgb(232, 243, 255);
  }
  .list {
    position: relative;
  }
  .page {
    position: absolute;
    right: 0;
    bottom: -50px;
  }

  .el-table--border:after,
  .el-table--group:after,
  .el-table:before {
    background-color: #3a8ee6;
  }

  .el-table--border,
  .el-table--group {
    border-color: #3a8ee6;
  }

  .addUserdefault1 {
    position: absolute;
    z-index: 1001;
    width: 470px;
    height: 435px;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .addUserdefault1 .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
  }
  .addUserdefault1 .title i {
    position: relative;
    top: -55px;
    right: -210px;
    color: black;
    font-size: 20px;
  }
  .addUserdefault1 .add {
    margin-right: 30px;
    margin-top: 25px;
  }
  .addUserdefault1 .addButton {
    margin-left:-25px;
    margin-top: -30px;
    position: relative;
    left:-10px;
  }
  .addUserdefault1 .addButton .el-button {
    width: 80px;
  }
  .addUserdefault1 .el-select {
    width: 280px;
  }

  .changeUserdefault1 {
    position: absolute;
    z-index: 1001;
    width: 470px;
    height: 435px;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  .changeUserdefault1 .title {
    height: 50px;
    width: 100%;
    text-align: center;
    line-height: 30px;
  }
  .changeUserdefault1 .title i {
    position: relative;
    top: -55px;
    right: -210px;
    color: black;
    font-size: 20px;
  }
  .changeUserdefault1 .change {
    margin-right: 30px;
    margin-top: 25px;
  }
  .changeUserdefault1 .saveButton {
    margin-left:-30px;
    position: relative;
    left:-20px;
    margin-top: -30px;
  }
  .changeUserdefault1 .saveButton .el-button {
    width: 80px;
  }
  .changeUserdefault1 .el-select {
    width: 280px;
  }

  .delete {
    position: absolute;
    z-index: 1001;
    width: 400px;
    height: 180px;
    border: 1px solid #dcdfe6;
    left: 0;
    top: -50px;
    bottom: 0;
    right: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
  }
  .title {
    width: 100%;
    height: 45px;
    line-height: 45px;
  }
  .title span {
    float: left;
    margin-left: 20px;
  }
  .delete .title i {
    float: right;
    margin-top: 10px;
    margin-right: 10px;
  }
  .delete .main {
    height: 60px;
    width: 100%;
    line-height: 50px;
    margin-top: 15px;
    margin-left: 0px;
  }
  .delete .main i {
    color: #e6a23c;
    font-size: 20px;
    margin-right: 10px;
  }
  .delete .main span {
    margin-bottom: 5px;
  }
  .delete .deleteButton {
    float: right;
    margin-right: 20px;
    margin-top: 5px;
  }
  .popContainer {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 500;
  }
</style>




